* {
    margin: 0;
    padding: 0;
}

/* signIn */
.signInParent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: scroll;
    height: 100vh;
    width: 100vw;
}

.signInHeading {
    font-size: 20px;
    font-weight: bold;
    color: #fdffff;
}

.signInBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1cm;
    row-gap: 0.3cm;
    background-color: rgb(39, 39, 39);
    width: fit-content;
    border-radius: 12px;
    width: 10cm;
}

.inputEmail,
.inputPassword {
    padding: 0.3cm;
    align-items: center;
    width: 7cm;
    border-radius: 6px;
    border: thin solid rgb(102, 102, 102);
    background-color: transparent;
    color: rgb(255, 255, 255);
    font-weight: bolder;
}

.inputEmail:focus,
.inputPassword:focus {
    outline: 3px solid rgb(0, 123, 255);
}

.Site {
     width: 100%;
    height: 100%; 
}

.body{
    width:100%;
    display: flex;
    flex-direction: column;
}

.lightBG{
    background-color: #fdffff;
}

.darkBG{
    background-color: rgb(18, 18, 18);
}

.lightText{
    color: white;
}

.darkText{
    color:black;
}

.logo {
    width: 6cm;
    height: auto;
}

.btnDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 0.2cm;
}

.active {
    color: rgb(137, 0, 217);
}

.inputSelect {
    padding: 0.3cm;
    align-items: center;
    width: 7.7cm;
    border-radius: 6px;
    border: thin solid rgb(102, 102, 102);
    background-color: transparent;
    color: rgb(244, 244, 244);
    font-weight: bolder;
}

.optionSelect {
    border: thin solid rgb(102, 102, 102);
    background-color: rgb(198, 143, 250);
    color: black;
}

input::placeholder {
    color: #f5f5f5;
    font-weight: 100;
}

.labelCheckBox {
    padding: 5px;
}

.labelText {
    color: rgb(255, 50, 50);
    font-size: 14px;
}

.createAccount {
    font-size: 14px;
    color: rgb(91, 203, 255);
    cursor: pointer;
    text-decoration: none;
}

.btn {
    border: 3px solid rgb(0, 136, 255);
    color: rgb(255, 255, 255);
    background-color: transparent;
    border-radius: 6px;
    cursor: pointer;
    padding: 0.2cm 0.3cm;
}

.Dark {
    color: rgb(238, 238, 238);
}

.Home {
    width: 100%;
}

.colDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.btn-disabled {
    border: 3px solid rgb(105, 105, 105);
    color: rgb(255, 255, 255);
    background-color: transparent;
    border-radius: 6px;
    padding: 0.2cm 0.3cm;
    cursor: not-allowed;
    pointer-events: none;
}

.btn:hover {
    /* transition: 1s ease; */
    background-color: rgb(0, 136, 255);
    color: rgb(255, 255, 255);
    border-radius: 6px;
    cursor: pointer;
}

#overlayDiv {
    display: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: 1;
    position: absolute;
}

.content-Home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Booklist {
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* padding: 1cm; */
}

.bookTable {
    justify-content: center;
    align-items: center;
    border: thin solid grey;
    width: 100%;
}

.bookCell {
    border: thin solid rgb(174, 174, 174);
    justify-content: center;
}

.text {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border-bottom: thin solid rgb(120, 120, 120);
}


/* notifications */

.notifications {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 0.5cm;
    border-radius: 8px;
    border: thin solid rgb(139, 139, 139);
    margin: 1cm;
    padding: 1cm;
}

.row1 {
    padding: 1cm;
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 1cm;
}

.profileImage {
    width: 10cm;
}

.BookList {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 0.5cm;
    column-gap: 0.5cm;
    padding: 1cm;
}

.searchBar {
    background-color: transparent;
    border: thin solid rgb(65, 65, 65);
    border-radius: 6px;
    padding: 0.5cm;
    align-items: center;
    width: 8cm;
    color: grey;
}

.searchBar::placeholder {
    color: rgb(160, 160, 160);
}

.lightCard {
    width: 5cm;
    border: 0.5px solid rgb(217, 217, 217);
    background-color: rgb(253, 253, 253);
    padding: 0.5cm;
    border-radius: 5px;
}

.lightCard:hover {
    background-color: rgb(241, 241, 241);
}

.darkCard {
    width: 5cm;
    border: thin solid rgb(41, 41, 41);
    background-color: rgb(40, 40, 40);
    padding: 0.5cm;
    border-radius: 5px;
}

.darkCard:hover {
    background-color: rgb(45, 45, 45);
}

.cardContent {
    display: flex;
    flex-direction: column;
    row-gap: 0.2cm;
}

.bookDisplay {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 0.5cm;
    row-gap: 0.5cm;
    overflow: auto;
}

.bookCover {
    width: 3cm;
}

.cardRow1 {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-bottom: thin solid grey;
    padding: 0.2cm;
}

.cardRow2 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2cm;
}

.wishListHeart {
    width: 0.8cm;
    cursor: pointer;
}

.btnOrder {
    background-color: rgb(2, 129, 2);
    color: white;
    border: none;
    padding: 0.2cm;
    cursor: pointer;
}

.btnOrder:hover {
    transition: ease;
    background-color: rgb(0, 102, 0);
}

.btnDelete {
    background-color: rgb(227, 11, 0);
    color: white;
    border: none;
    padding: 0.2cm;
    cursor: pointer;
}

.btnDelete:hover {
    transition: ease;
    background-color: rgb(162, 0, 0);
}

.cardRow3 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 0.2cm;

}

.bookName,
.bookAuthor,
.bookGenre {
    border-bottom: thin solid rgb(94, 94, 94);
}

#addBookParent,
#confirmDelete {
    display: none;
}

.addBookParent {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 1;
    background-color: rgba(120, 120, 120, 0.942);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.addBookChild {
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 0.5cm;
    padding: 1cm;
}

.attachment {
    display: none;
}

.bookBox {
    display: flex;
    flex-direction: row;
    column-gap: 0.5cm;
    align-items: center;
    justify-content: center;
}

.clip {
    width: 2cm;
}

.requestPage {
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.requestContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.BookPage {
    padding: 0.5cm;
    display: flex;
    flex-direction: column;
    row-gap: 0.5cm;

}